<template>
  <div class="our-layout" >
    <template>
      <transition name="fade-in-out">
        <commonTopNav class="common-top-nav" v-if="cntShow"></commonTopNav>
      </transition>
      <transition name="slide-fade">
        <p class="flex title"
           v-if="wordShow">
          <span>{{ content }}</span>
          <span class="cursor" :class="{flicker:isflicker}"></span>
          <audio src="../../assets/audio/typeAudio.mp3" autoplay="" id="sound" muted></audio>
        </p>
      </transition>

      <transition name="fade-in-out">
        <p class="flex time" v-if="timeShow">2021.{{ time }}</p>
      </transition>

      <transition name="fade-in-out">
          <kind></kind>
      </transition>

      <transition name="slide-fade">
        <div class="skip flex"  v-if="skipShow">
          <p class="sound-tip">戴耳机，聆听键盘音效</p>
          <div class="skip-btn"
               @click="showCnt('hide')">
            跳过，不再出现
          </div>
        </div>
      </transition>
    </template>
  </div>
</template>

<script>
import Kind from "@/components/kind-our2021";
import commonTopNav from "@/components/common-top-nav";
export default {
  name: "our2021",
  components: {
    Kind,
    commonTopNav
  },
  data() {
    return {
      title: '开荒团又相伴走过了一年，2021，不平凡的一年...',
      content: '',
      count: 0,
      isflicker: false,
      wordShow: false,
      timeShow: false,
      cntShow: false,
      skipShow:false,
      time: '01',
      timer: '',
      fail:true,
      password:''
    }
  },
  mounted() {
   if(!localStorage.getItem("showWord")){
     this.wordShow=true
     this.skipShow= true
     this.typeWord()
         .then(() => {
           this.title = '我们经历了很多，也成长了很多，总有人为了时间消磨生命！'
           return this.typeWord()
         })
         .then(() => {
           this.title = '再见，2021 ！你好，开荒 ！'
           return this.typeWord()
         })
         .then(() => {
           if (this.wordShow)
             return this.showTime()
         })
         .then(() => {
           return this.countDown()
         })
         .then(() => {
           this.showCnt()
         })
   }else{
     this.showCnt();
   }
 },
  methods: {
    playAudio(type){
      let sound = document.getElementById('sound')
      if(type==='pause'){
        sound.pause();
      }else{
        sound.play()
      }
    },
    typeWord() {
      return new Promise(resolve => {
        this.isflicker = false
        const timer = setInterval(() => {
          if (this.count < this.title.length) {
            this.count++
            this.content = this.title.substr(0, this.count)
            this.playAudio('play')
          } else {
            this.playAudio('pause')
            this.count = 0
            clearInterval(timer)
            this.isflicker = true
            setTimeout(() => {
              resolve()
            }, 800)
          }
        }, 250)
      })
    },
    showTime() {
      return new Promise(resolve => {
        this.wordShow = false
        this.timeShow = true
        setTimeout(function () {
          resolve()
        }, 1000)
      })
    },
    countDown() {
      return new Promise(resolve => {
        let timeCount=1
        this.timer = setInterval(() => {
          if (timeCount < 13) {
            this.time=timeCount > 9 ? timeCount++ :'0'+timeCount++
          } else {
            clearInterval(this.timer)
            setTimeout(function () {
              resolve()
            }, 500)
          }
        }, 250)
      })
    },
    showCnt(hide) {
      if(hide){
        localStorage.setItem("showWord","hide")
      }
      clearInterval(this.timer)
      this.timeShow = false
      this.wordShow = false
      this.skipShow = false
      this.cntShow = true
    }
  }
}
</script>

<style scoped>
.our-layout {
  height: 100%;
  width: 100%;
  background-color: #111115;
  overflow: hidden;
}

.title,
.time {
  color: #fff;
  height: 100%;
  line-height: 100%;
  font-size: 20px;
}

.time {
  font-size: 82px;
  font-weight: bold;
}

.cursor {
  display:inline-block;
  height: 24px;
  width: 1px;
  background-color: #fff;
  margin-left:6px;

}

.flicker {
  animation: flicker .65s infinite;
  background-color: transparent !important;
}
.sound-tip{
  color:#666;
  padding-bottom: 12px;
}
.skip {
  position: fixed;
  bottom: 250px;
  margin: auto;
  left: 0;
  right: 0;
  width: 160px;
  flex-direction: column;
}
.skip-btn{
  color: #fff;
  padding: 10px 25px;
  font-size: 18px;
  border: 1px solid #f5f5f5;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  transition: all .3s ease;
}

.skip-btn:hover {
  background: #fff;
  color: #333;
}

.common-top-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background: none;
}

@keyframes flicker {
  from, to { box-shadow: 1px 0 0 0 transparent; }
  50% { box-shadow: 1px 0 0 0 ; }

}

.slide-fade-enter-active {
  transition: all .3s ease;
}

.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.slide-fade-enter, .slide-fade-leave-to {
  transform: translateY(10px);
  opacity: 0;
}
.fade-in-out-enter-active{
  animation: fade-in-out 1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-in-out-leave-active{
  animation: fade-in-out 1s cubic-bezier(1.0, 0.5, 0.8, 1.0) reverse;
}
@keyframes fade-in-out {
  from{
    transform: translateY(-25px);
    opacity: 0;
  }
  to{
    transform: translateY(0);
    opacity: 1;
  }
}


</style>