<template>
  <div class="content-layout flex">
      <div class="content-inner flex" v-for="list in navList" :key="list.id" :id="list.id">
        <div class="item-group-wrap">
          <div class="flex group-title-wrap">
            <component :is="list.icon" theme="outline" size="18" fill="#777"></component>
            <h1 class="group-title">{{ list.title }}</h1>
          </div>
          <div class="group-list">
            <ul class="list-wrap flex">
              <li class="list-item" v-for="item in list.navGroup" :key="item.id" :title="item.navDes">
                <a :href="item.link" class="flex item-link" target="_blank">
                  <img :src="item.navIcon" alt="icon" class="item-img">
                  <div class="item-content">
                    <h2 class="item-title">{{ item.navName }}</h2>
                    <p class="item-des">{{ item.navDes }}</p>
                  </div>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
  </div>

</template>

<script>
import {Camera, Iphone} from '@icon-park/vue'
import {store, mutation} from '@/store.js'

export default {
  name: "nav-content",
  computed: {
    navList() {
      return store.state.navList
    }
  },
  components: {
    Camera, Iphone
  },
  mounted() {
    document.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll(e) {
      let scrollTop = e.target.scrollTop || document.documentElement.scrollTop || document.body.scrollTop
      let num = this.navList.length
      for (let i = 0; i < num; i++) {
        let isScrollEnd = document.querySelector('#' + this.navList[i].id).offsetTop < scrollTop + 86
        if (isScrollEnd) {
          mutation.setNum(i)
        }
      }
    }
  }
}
</script>

<style scoped>

.content-layout {
  width: 100%;
  padding-top: 20px;
  overflow: hidden;
  box-sizing: border-box;
  flex-direction: column;
}

.content-inner {
  box-sizing: border-box;
  margin-bottom: 20px;
  padding-top: 20px;
  flex: 1;
}


.item-group-wrap {
  background-color: #fff;
  border-radius: 12px;
  padding: 30px 20px;
  text-align: left;
}

.group-title-wrap {
  justify-content: start;
  margin-bottom: 15px;
  margin-left: 10px;
}

.group-title {
  padding-left: 6px;
}

.list-wrap {
  flex-wrap: wrap;
  justify-content: start;
}

.list-item {
  overflow: hidden;
  box-sizing: border-box;
  border: 1px solid #f5f5f5;
  border-radius: 12px;
  margin: 10px;
  width: 240px;
  transition: all .3s ease-in;
}
.list-item:hover{
  box-shadow: 0  5px 6px rgba(0,0,0,0.15);
  transform:translateY(-5px);
}

.item-link {
  overflow: hidden;
  box-sizing: border-box;
  padding: 10px;
}

.item-link:hover {
  text-decoration: none;
}

.item-link:hover .item-title {
  color: #ec8f0e;
}
.item-link:hover .item-des {
  color: #282c34;
}

.item-img {
  width: 56px;
  height: 56px;
  overflow: hidden;
  flex: 0 0 auto;
  transition: all .3s ease-in;
}

.item-content {
  padding-left: 10px;
  width: calc(100% - 66px);
}

.item-title {
  font-size: 15px;
  font-weight: bold;
  color: #151212;
  line-height: 2;
  transition: all .2s ease-in;
}

.item-des {
  font-size: 12px;
  color: #8f8f8f;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: all .2s ease-in;
}
/*深色模式*/
:root[mode=dark] .item-group-wrap{
  background-color: #383838;
}
:root[mode=dark] .list-item{
  border: 1px solid #414141;
}

:root[mode=dark] .item-title{
  color:#f3fdff;
}
:root[mode=dark] .group-title{
  color: #b5b9bb;
}
:root[mode=dark] .item-img{
  filter:brightness(0.8) contrast(1.2);
}
:root[mode=dark] .list-item:hover{
  box-shadow: 0  5px 6px rgba(0,0,0,0.25);
  transform:translateY(-5px);
  background-color: #353535;
}
:root[mode=dark] .item-link:hover .item-img{
  filter:brightness(1) contrast(1.2);
}
:root[mode=dark] .item-link:hover .item-title {
  color: #1352ee;
}
:root[mode=dark] .item-link:hover .item-des {
  color: #d8dce0;
}


@media screen and (min-width: 1600px ) {
  .content-inner {
    width: 1340px;
  }
}

@media screen and (max-width: 1600px) {
  .content-inner {
    width: 1080px;
  }
}

@media screen and (max-width: 1400px) {
  .content-inner {
    width: 820px;
  }
}

@media screen and (max-width: 1120px) {
  .content-inner {
    width: 560px;
  }
}

@media screen and (max-width: 800px) {
  .content-inner {
    width: calc(100% - 30px)
  }

  .list-item {
    width: 100%;
    box-sizing: border-box;
  }

}

@media screen and (max-width: 500px) {
  .content-layout {
    padding-top: 86px;
  }

  .content-inner {
    display: block;
    width: 100%;
  }

  .item-group-wrap {
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    padding: 30px 15px;
  }

  .list-wrap {
    width: 100%;
    box-sizing: border-box;
    display: block;
  }

  .list-item {
    display: block;
    width: auto;
  }
}

</style>
