<template>
  <div class="week-exercise-layout">

    <div class="nav">
      <commonTopNav>
        <router-link to="/our2021" class="nav-item">我们2021</router-link>
      </commonTopNav>
    </div>
    <div v-if="isCeiling" class="nav-placeholder"></div>
    <div class="flex flex-between  week-nav" :class="{'week-nav-ceiling':isCeiling}">
      <div
          class="week-nav-item"
          v-for="(item,index) in weekNavList"
          :key="index"
          :class="[item.classList,{active:currenIndex===index}]"
          @click="toggleIndex(index)"
      >
        <router-link :to="item.link" class="nav-link flex">
          <component :is="item.icon" theme="filled" size="32" fill="#fff" :strokeWidth="2">
          </component>
          <span class="nav-name">{{ item.name }}</span>
        </router-link>
      </div>
    </div>
    <div class="cnt">
      <transition name="slide" mode="out-in">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>
    </div>
    <index-footer></index-footer>
    <div v-show="showArrow" class="go-top-wrap flex" @click="goTop">
      <to-top theme="filled" size="24" fill="currentColor" :strokeWidth="3" />
    </div>
  </div>
</template>

<script>
import commonTopNav from "@/components/common-top-nav"
import {store, mutation} from '@/store.js'
import indexFooter from "@/components/index-footer";

export default {
  name: "week-exercise",
  components: {
    commonTopNav,
    indexFooter
  },
  data() {
    return {
      weekNavList: [{
        name: "三维组",
        link: "/our2021/weekExercise/3d",
        classList: ["nav-3d"],
        icon: "CoordinateSystem",
      }, {
        name: "平面组",
        link: "/our2021/weekExercise/plan",
        classList: ["nav-plan"],
        icon: "ContrastViewCircle",
      }, {
        name: "动画组",
        link: "/our2021/weekExercise/animation",
        classList: ["nav-animation"],
        icon: "CarouselVideo",
      },
      ],
      isCeiling: false,
      showArrow:false
    }
  },
  computed: {
    currenIndex() {
      return store.state.weekCurrentIndex
    }
  },
  methods: {
    toggleIndex(index) {
      mutation.setWeekCurrentIndex(index)
      this.goTop()
    },
    setCeiling(e) {
      let scrollTop = e.target.scrollTop || document.documentElement.scrollTop || document.body.scrollTop
      let screenHeight=window.screen.height
      this.isCeiling = scrollTop > 165;
      this.showArrow =scrollTop > screenHeight;
    },
    goTop(){
      document.querySelector('body').scrollIntoView({
        behavior: 'smooth'
      })
    }
  },
  mounted(){
    document.addEventListener('scroll',this.setCeiling,true)
  }
}
</script>

<style scoped>
.week-exercise-layout {
  width: 100%;
  min-height: 100vh;
  background-color: #16161c;
  overflow: hidden;

}

.nav {
  background-color: #16161c;
}

.common-top-nav-layout {
  width: 1200px;
  justify-content: left;
  margin: auto;
  padding-bottom: 60px;
}

.week-nav {
  width: 1200px;
  margin: auto;
  padding: 20px 0;
  z-index: 99;
  position: relative;
}

.week-nav-ceiling{
  position: fixed;
  left:50%;
  margin-left: -600px;
  z-index: 999;
  top:0;
  background-color: #16161c;
  box-shadow: 0 6px 18px #111111;
}

.nav-placeholder{
  width: 100%;
  height:154px ;
}
.week-nav-item {
  color: #fff;
  border: 1px solid #2c2c2c;
  background-color: #19191f;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  transition: all .4s cubic-bezier(.71, .22, .58, .84);
}

.week-nav-item:not(.active):hover {
  transform: scale(1.1);
}

.week-nav-item.active {
  box-shadow: 0 24px 15px -12px #000;
  transform: translateY(-6px);
}

.nav-link {
  width: 100%;
  height: 100%;
  color: #fff;
  padding: 30px 120px;
  box-sizing: border-box;
}

.nav-name {
  margin-left: 10px;
}

.cnt {
  width: 1200px;
  margin: auto;
}
.go-top-wrap{
  position: fixed;
  bottom:120px;
  right: 50%;
  margin-right: -680px;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color:rgba(100,100,100,.5);
  cursor: pointer;
  color:#ccc;
}
.nav-3d.active {
  border: 1px solid #86fde8;
  background-image: linear-gradient(45deg, #a0ace3, #77f2dc);
}

.nav-plan.active {
  border: 1px solid #9796f0;
  background-image: linear-gradient(45deg, #9796f0, #fbc7d4);
}

.nav-animation.active {
  border: 1px solid #43c7ad;
  background-image: linear-gradient(45deg, #43c7ad, #F4D03F);
}

.slide-enter-active {
  transition: all .8s cubic-bezier(.71, .22, .58, .84);
}

.slide-leave-active {
  transition: all .5s cubic-bezier(.71, .22, .58, .84);
}

.slide-enter {
  opacity: 0;
  transform: translateY(-50px);
}

.slide-leave-to {
  opacity: 0;
  transform: translateY(20%);
}

</style>