<template>
  <div class="week">
    <div class="flex explain-box">
      <div class="explain flex">
        <div>
          <p class="explain-title">{{weekData.kindInfo.kindName}}<br>入选说明和统计</p>
          <p class="explain-text">{{weekData.kindInfo.kindRequest}}</p>
          <p class="explain-text" v-html="weekData.kindInfo.kindMembers"></p>
        </div>
      </div>
      <swiper
          v-if="isOk"
          ref="mySwiper"
          :options="swiperOption"
          class="swiper-box swiper"
      >
        <swiper-slide
            class="swiper-slide"
            v-for="(item,index) in weekData.slideshowList"
            :key="index"
            >
          <div>
            <img :src="item.img"  alt="作品轮播" class="slide-img">
          </div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="portfolio">
      <h3 class="portfolio-title">入选组员</h3>
      <member class="member-box" :member-list="weekData.memberList" :scroll-top="300"></member>
      <h3 class="portfolio-title">练习作品集</h3>
      <div class="product-box flex">
        <div class="prod-item"
             v-for="(item,index) in weekData.productList"
             :key="index"
             @click="showBigImg(item.imgBig,item.name)"
            >
          <photo-card :item="item"><slot></slot></photo-card>
        </div>
        <transition name="fade" mode="out-in" >
          <div v-show="isHide" class="zoom-box flex"  @click="hideBigImg"  v-lazy-container="{ selector: 'img' }"  >
              <img :data-src="imgBig" class="zoom-img" alt="大图预览" :data-error="errorImg" :data-loading="loadImg">
              <p class="author-name">{{authorName}}</p>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import PhotoCard from "@/components/photo-card";
import {mutation} from '@/store.js'
import member from "@/components/member";
export default {
  name: "week-3d",
  components: {
    PhotoCard,
    member
  },
  props:['weekData'],
  data() {
    return {
      swiperOption: {
        direction: 'vertical',
        autoplay: {
          delay: 3000 // 3秒切换一次
        },
        loop: true
      },
      loadImg:require('../assets/big-img-loading.gif'),
      errorImg:require('../assets/big-error.png'),
      currenIndex: '',
      isHide: false,
      imgBig: '',
      authorName:'',
      isOk:true,
      timer:''
    }
  },
  methods: {
    showBigImg(bigImg,name) {
        this.isHide = true
        this.imgBig = bigImg
        this.authorName=name
    },
    hideBigImg(){
      this.isHide=false
      this.imgBig=''
      this.authorName=''
    },
    toggleNav(){
      switch (this.$route.name) {
        case 'week-plan':
          mutation.setWeekCurrentIndex(1)
          break
        case 'week-animation':
          mutation.setWeekCurrentIndex(2)
          break
        default:
          mutation.setWeekCurrentIndex(0)
      }
    }
  },
  activated() {
    clearTimeout(this.timer)
    this.isOk=true
    this.toggleNav()
  },
  deactivated() {
   this.timer= setTimeout(()=>{
     this.isOk=false
   },300)

  }
}
</script>

<style scoped>
.week {
  padding: 60px 0 30px;
}

.explain {
  width: 50%;
  height: 360px;
  box-sizing: border-box;
  padding: 80px;
  text-align: left;
  color: #f5f5f5;
  background-color: #111111;
}

.swiper-box {
  width: 50%;
  height: 360px;
}

.explain-title {
  font-size: 30px;
  letter-spacing: 2px;
  line-height: 1.45;
  margin-bottom: 20px;
}
.member-box{
  margin: 10px 0 40px;
  justify-content: flex-start;
}
.explain-text {
  line-height: 1.68;
  padding-bottom: 5px;
}

.explain-box {
  border-radius: 6px;
  overflow: hidden;
}

.portfolio {
  padding-top: 50px;
}

.portfolio-title {
  color: #fff;
  font-size: 16px;
  text-align: left;
  line-height: 1.45;
  padding-bottom: 20px;
}

.product-box {
  padding: 10px 0;
  justify-content: flex-start;
  margin-left: -20px;
  flex-wrap: wrap;
}

.prod-item {
  width: 285px;
  overflow: hidden;
  margin-left: 20px;
  margin-bottom:20px;
}
.slide-img{

}

.prod-item:hover {
  box-shadow: 0 24px 15px -12px #000;
}
.author-name{
  text-align: center;
  height: 24px;
  width: 120px;
  line-height: 24px;
  color:#363636;
  font-size:12px;
  background: rgba(255, 255, 255, .6) !important;
  backdrop-filter: blur(10px) !important;
  margin-top:-34px;
  border-radius: 24px;
}

.zoom-box {
  width:100%;
  height: 100%;
  box-sizing: border-box;
  background: rgba(0, 0, 0, .7) !important;
  backdrop-filter: blur(15px) !important;
  position: fixed;
  z-index: 9999;
  top: 0;
  left:0;
  bottom:0;
  right: 0;
  flex-direction: column;
  padding:20px;
}
.zoom-img{
  max-height: 100%;
  max-width: 100%;
  pointer-events: none;
}

.fade-leave-active {
  transition: all .2s ease-in;
}
.fade-enter-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);

}
.fade-leave-to,
.fade-enter {
  transform: scale(0.3);
  opacity: 0;
}


</style>