<template>
  <div class="member-layout flex"
       @mousemove="moveBounce"
       @mouseleave="leaveBounce">
    <div v-for="(item,index) in memberList"
         :key="index"
         class="member-item"
    >
      <img :src="item.img"
           alt="头像"
           :class="{'member-img':isBounce}"
           :style="{animationDuration:(index/10)+0.3+'s'}"
      >
      <p class="member-name">{{ item.name }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "member",
  props: ['memberList', 'scrollTop'],
  data() {
    return {
      isBounce: false,
      timer: '',
      isOne:true
    }
  },
  mounted() {
    document.addEventListener('scroll', this.bounceImg, true)
  },
  methods: {
    moveBounce() {
      if (!this.isBounce) {
        this.isBounce = true
        setTimeout(() => {
          this.isBounce = false;
        }, (this.memberList.length / 10 + 0.3) * 1000)
      }
    },
    leaveBounce() {
      if (!this.isBounce) {
        setTimeout(() => {
          this.isBounce = false;
        }, (this.memberList.length / 10 + 0.3) * 1000)
      }
    },
    bounceImg(e) {
      let scrollTop = e.target.scrollTop || document.documentElement.scrollTop || document.body.scrollTop
      if(scrollTop <= this.scrollTop){
        this.isOne=true;
        return this.isOne
      }
      if(this.isOne){
          this.isBounce=true;
          this.isOne=false;
          this.timer = setTimeout(() => {
            this.isBounce = false
            clearTimeout(this.timer)
          }, (this.memberList.length / 10 + 0.3) * 1000)
      }
    }
  }
}
</script>

<style scoped>
.member-layout {
  flex-wrap: wrap;
}

.member-item {
  margin-right: 20px;
  margin-bottom: 20px;
}

.member-item:last-child {
  margin-right: 0;
}

.member-img {
  animation-name: bounce;
  animation-timing-function: cubic-bezier(.68, .32, .44, .81);
}

.member-name {
  color: #fff;
  line-height: 2;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(-20px);
  }
  75% {
    transform: translateY(-10px);
  }
  0% {
    transform: translateY(0);
  }
}
</style>