export const mutation = {
    setWeekCurrentIndex(index){
        store.state.weekCurrentIndex=index
    },
    setNum(num) {
        store.state.num = num
    },
    setMode(mode,follow,init){
        if(mode==='dark'){
            document.getElementById('root').setAttribute('mode','dark')
            if(init!=="init"){
                document.getElementById('body-el').classList.add('light-to-dark')
                setTimeout(()=>{
                    document.getElementById('body-el').classList.remove('light-to-dark')
                },600)
            }
        }else{
            document.getElementById('root').removeAttribute('mode')
            if(init!=="init") {
                document.getElementById('body-el').classList.add('dark-to-light')
                setTimeout(() => {
                    document.getElementById('body-el').classList.remove('dark-to-light')
                }, 600)
            }
        }
        store.state.mode=mode
        store.state.follow=follow
    }
}
export const action = {
    initMode(init){
        //匹配系统设置，初始化mode
        const cachedMode=localStorage.mode ? localStorage.mode :false
        const followSystemMode= localStorage.follow ? localStorage.follow :false
        const systemMode = window.matchMedia('(prefers-color-scheme:dark)').matches
        if(followSystemMode || !cachedMode){
            if(systemMode){
                mutation.setMode('dark',followSystemMode,init)
            }else{
                mutation.setMode('light',followSystemMode,init)
            }
        }else{
            mutation.setMode(localStorage.mode,followSystemMode,init)
        }
    }
}
