<template>
  <div class="nav-menu-layout">
    <router-link  to='/' >
      <div class=" header-layout">
        <img :src="setLogoImg" alt="logo" class="logo-img">
        <p class="header-des">知识海洋的灯塔</p>
      </div>
    </router-link>

    <div class="list-layout">

      <ul>
        <li v-for="(list,index) in navList"
            :key="list.id"
            class="list-item flex"
            :class="{'active-item' : setActiveIndex === index}"
            @click="goAnchor('#'+list.id)">
          <div class="flex item-link">
            <component
                :is="list.icon"
                theme="outline"
                size="22"
                :strokeWidth="3"
                :fill="setActiveIndex === index ? iconColor : '#777'"
                class="list-icon"
            >
            </component>
            <p class="item-name">{{ list.title }}</p>
          </div>
        </li>
      </ul>
    </div>
    <div class="mode-toggle-layout flex">
      <div class="active-tabs-bg" :class="{'moon':getMode==='dark','follow':getFollow}"></div>
      <div class="mode-item flex"
           :class="[{'active':index===activeIndex},colorArray[activeIndex]]"
           v-for="(item,index) in iconList"
           :key="index"
           @click="toggleMode(index)"
           :title="item.title"
      >
        <component
            :is="item.name"
            theme="filled"
            :size="item.size"
            fill="currentColor"
            :strokeWidth="3"
            class="i"
            >
        </component>
      </div>
    </div>
    <div class="tip-layout" :class="{'tip-layout-hide':!showTip}">{{modeTitle[activeIndex]}}</div>
    <div class="mask"></div>
  </div>
</template>

<script>

import {action, store} from '@/store'

export default {
  name: "nav-menu",
  data(){
    return {
      activeIndex:'',
      showTip:true,
      iconList:[
        {name:'sun-one',size:16,title:'亮色模式'},
        {name:'moon',size:14,title:'黑暗模式'},
        {name:'laptop-computer',size:16,title:'跟随系统'},
      ],
      colorArray:[
          "sun","moon","follow"
      ],
      modeTitle:[
          "亮色模式","黑暗模式","跟随系统"
      ]
    }
  },
  created() {
    if(store.state.follow){
      return this.activeIndex=2;
    }
    if(store.state.mode === "dark"){
      return this.activeIndex=1;
    }
    this.activeIndex=0;
    this.toggleTip();
  },
  methods: {
    toggleTip(){
      this.showTip=true;
      if(!this.timer){
        this.timer = setInterval(()=>{
          this.showTip=false;
          clearInterval(this.timer);
          this.timer=null;
        },1000)
      }
    },
    goAnchor(sel) {
      document.querySelector(sel).scrollIntoView({
        behavior: 'smooth',
        block:'start'
      })
    },
    toggleMode(index){

      if(index !== this.activeIndex){
        this.activeIndex = index
        this.toggleTip();
        switch (index){
          case 0:
            localStorage.mode = "light"
            localStorage.removeItem('follow')
            action.initMode()
            break
          case 1:
            localStorage.mode="dark"
            localStorage.removeItem('follow')
            action.initMode()
            break
          case 2:
            localStorage.follow = "true"
            action.initMode()
        }
      }
    }
  },
  computed: {
    getMode(){
      return store.state.mode
    },
    getFollow(){
      return store.state.follow
    },
    navList() {
      return store.state.navList
    },
    setActiveIndex() {
      return store.state.num
    },
    setLogoImg(){
      return store.state.mode==='dark' ? require('../assets/logo.png') : require('../assets/logo-light.png')
    },
    iconColor(){
      return store.state.mode==='dark' ? '#fff' : '#000'
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}
.nav-menu-layout {
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow: hidden;
}

.header-layout {
  padding-top: 60px;
  color: #fff;
  width: 100%;
}

.header-des {
  letter-spacing: 2px;
  color: #666;
}

.list-layout {
  padding-top: 45px;
}

.list-item {
  padding: 10px 0;
  cursor: pointer;
  transition: all .2s ease-in;
}
.list-item:not(.active-item):hover {
  background-color: #f5f5f5;
}
.list-icon{
  transition: all .3s ease-in;
}
.list-item:not(.active-item):hover .list-icon{
  transform: rotate(180deg);
}
.item-name {
  color: #777777;
  padding-left: 10px;
}

.active-item .item-name {
  color: #000;
  font-size: 15px;
  font-weight: 600;
}

.item-link {
  position: relative;
  cursor: pointer;
}

.item-link:hover,
.item-link:active {
  text-decoration: none;
}

.active-item .item-link:before {
  display: block;
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  top: 6px;
  left: -20px;
  background-color: #f29e26;
}
.nav-menu-layout .mask {
  pointer-events: none;
  border-radius: 100%;
  filter: blur(50px);
  height: 300px;
  width: 300px;
  top: 300px;
  left: 0;
  z-index: 90;
  position: absolute;
}
.nav-menu-layout .mask {
  -webkit-animation: mask 25s linear infinite;
  animation: mask 25s linear infinite;
}

@-webkit-keyframes mask {
  0% {
    transform: translate(-30%,-30%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,189,46,.1) 0,rgba(255,189,46,0) 100%);
  }

  25% {
    transform: translateY(30%)
  }

  50% {
    transform: translate(30%,80%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,69,79,.1) 0,rgba(255,69,79,0) 100%)
  }

  75% {
    transform: translate(30%,-80%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(7,193,96,.1) 0,rgba(7,193,96,0) 100%)
  }

  to {
    transform: translate(-30%,-30%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,189,46,.1) 0,rgba(255,189,46,0) 100%);
  }
}

@keyframes mask{
  0% {
    transform: translate(-40%,-40%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,189,46,.1) 0,rgba(255,189,46,0) 100%);
  }

  25% {
    transform: translateY(40%)
  }

  50% {
    transform: translate(40%,60%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,69,79,.1) 0,rgba(255,69,79,0) 100%)
  }

  75% {
    transform: translate(40%,-60%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(7,193,96,.1) 0,rgba(7,193,96,0) 100%)
  }

  to {
    transform: translate(-40%,-40%);
    background: radial-gradient(50% 50% at 50% 50%,rgba(255,189,46,.1) 0,rgba(255,189,46,0) 100%);
  }
}
.mode-toggle-layout{
  background-color: #f3f3f3;
  width: 120px;
  height: 30px;
  padding:2px;
  margin:  30px auto 0;
  border-radius: 3px;
  position: relative;
  box-sizing: border-box;
}

.active-tabs-bg{
  transform: translateX(0);
  position: absolute;
  left:2px;
  top:2px;
  width: 36px;
  height: 26px;
  border-radius: 2px;
  background-color: #fff;
  box-shadow:  0 2px 2px rgba(0,0,0,0.1);
  transition: all .3s ease-in;

}
.active-tabs-bg.moon{
  transform: translateX(40px);
}
.active-tabs-bg.follow{
  transform: translateX(80px);
}
.mode-item{
  width: 40px;
  height: 100%;
  position: relative;
  z-index: 99;
  cursor:pointer;
  color:#b0b0b0;
}
.mode-item:not(.active):hover{
  color:#5f5f5f;
}
.mode-item.active.sun{
  color:#ffbd2e;
}
.mode-item.active.moon{
  color:#264df3;
}
.mode-item.active.follow{
  color:#47b159;
}

.tip-layout{
  text-align: center;
  line-height: 1.5;
  padding:10px;
  color:#8f8f8f;
  transition: all .3s ease-in;
  opacity: 1;
}
.tip-layout-hide{
  opacity: 0;
}
.logo-img{
  width: 120px;
}


/*深色模式*/

:root[mode=dark] .nav-menu-layout {
  background-color: #151212;
}

:root[mode=dark] .header-layout {
  color: #fff;
}

:root[mode=dark] .header-des {
  color: #777777;
}


:root[mode=dark] .list-item:not(.active-item):hover {
  background-color: #242424;
}
:root[mode=dark] .list-item:not(.active-item):hover .item-name {
  color:#ccc;
}

:root[mode=dark] .item-name {
  color: #777777;
}

:root[mode=dark] .active-item .item-name {
  color: #f8f8f8;
  font-weight: normal;
}

:root[mode=dark] .active-item .item-link:before {
  background-color: #f29e26;
}
:root[mode=dark] .mode-toggle-layout{
  background-color: #242424;
}
:root[mode=dark] .active-tabs-bg{
  background-color:#383838;
}
:root[mode=dark]  .active-item .item-link:before {
  background-color: #264df3;
}
</style>
