<template>
  <div class="nav-menu-layout flex">
    <div class="list-layout">
      <ul class="list-box">
        <li v-for="(list,index) in navList"
            :key="list.id"
            class="list-item flex"
            :class="{'active-item' : setActiveIndex === index}"
            @click="goAnchor('#'+list.id)">
          <div class="flex item-link">
            <component
                :is="list.icon"
                theme="outline"
                size="24"
                :strokeWidth="3"
                :fill="setActiveIndex === index ? iconColor : '#777'">
            </component>
            <p class="item-name">{{ list.title }}</p>
          </div>
        </li>
      </ul>
      <div class="mode-toggle-layout flex">
        <div class="active-tabs-bg" :class="{'moon':getMode==='dark','follow':getFollow}"></div>
        <div class="mode-item flex"
             :class="[{'active':index===activeIndex},colorArray[activeIndex]]"
             v-for="(item,index) in iconList"
             :key="index"
             @click="toggleMode(index)"
             :title="item.title"
        >
          <component
              :is="item.name"
              theme="filled"
              :size="item.size"
              fill="currentColor"
              :strokeWidth="3"
              class="i"
          >
          </component>
        </div>
      </div>
      <div class="tip-layout" :class="{'tip-layout-hide':!showTip}">{{modeTitle[activeIndex]}}</div>
    </div>

    <div class="mask"></div>
  </div>
</template>

<script>

import {store , action } from '@/store'
import smoothscroll from 'smoothscroll-polyfill'

export default {
  name: "nav-menu",
  data(){
    return {
      activeIndex:'',
      showTip:true,
      iconList:[
        {name:'sun-one',size:16,title:'亮色模式'},
        {name:'moon',size:14,title:'黑暗模式'},
        {name:'laptop-computer',size:16,title:'跟随系统'},
      ],
      colorArray:[
        "sun","moon","follow"
      ],
      modeTitle:[
        "亮色模式","黑暗模式","跟随系统"
      ]
    }
  },
  created() {
    if(store.state.follow){
      return this.activeIndex=2;
    }
    if(store.state.mode === "dark"){
      return this.activeIndex=1;
    }
    this.activeIndex=0;
    this.toggleTip();
  },
  methods: {
    goAnchor(sel) {
      document.querySelector('html').classList.remove('noscroll')
      this.$emit('toggle-nav', this.isClose)
      let offsetTopHeight = document.querySelector(sel).offsetTop
      smoothscroll.polyfill()
      window.scrollTo({
        top: offsetTopHeight - 80,
        behavior: 'smooth'
      });
    },
    toggleTip(){
      this.showTip=true;
      if(!this.timer){
        this.timer = setInterval(()=>{
          this.showTip=false;
          clearInterval(this.timer);
          this.timer=null;
        },1500)
      }
    },
    toggleMode(index){
      if(index !== this.activeIndex){
        this.activeIndex = index
        this.toggleTip();
        switch (index){
          case 0:
            localStorage.mode = "light"
            localStorage.removeItem('follow')
            action.initMode()
            break
          case 1:
            localStorage.mode="dark"
            localStorage.removeItem('follow')
            action.initMode()
            break
          case 2:
            localStorage.follow = "true"
            action.initMode()
        }
      }
    }
  },
  computed: {
    getMode(){
      return store.state.mode
    },
    getFollow(){
      return store.state.follow
    },
    navList() {
      return store.state.navList
    },
    setActiveIndex() {
      return store.state.num
    },
    iconColor(){
      return store.state.mode==='dark' ? '#fff' : '#000'
    }
  }
}
</script>

<style scoped>
.nav-menu-layout {
  width: 100%;
  height:calc(100% - 86px);
  background-color: #fff;
}

.list-layout {
  flex: 1 1 auto;
  height: 100%;
}
.list-box{
  height:calc(100% - 125px);
  overflow-y: scroll;
  padding-top:30px;
}

.list-item {
  padding: 8px 0;
  cursor: pointer;
}

.list-item:not(.active-item):hover {
  background-color: #f5f5f5;
}

.item-name {
  color: #777777;
  padding-left: 10px;
  font-size: 15px;
}

.active-item .item-name {
  color: #000;
  font-size: 17px;
}

.item-link {
  position: relative;
  cursor: pointer;
}

.item-link:hover,
.item-link:active {
  text-decoration: none;
}

.active-item .item-link:before {
  display: block;
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  top: 6px;
  left: -20px;
  background-color: #f29e26;
}

.mode-toggle-layout{
  background-color: #f3f3f3;
  width: 120px;
  height: 30px;
  padding:2px;
  margin:  25px auto 0;
  border-radius: 3px;
  position: relative;
  box-sizing: border-box;
  color:#fff;
}
.active-tabs-bg{

  position: absolute;
  left:2px;
  top:2px;
  width: 36px;
  height: 26px;
  border-radius: 2px;
  background-color:currentColor;
  box-shadow:  0 2px 2px rgba(0,0,0,0.1);
  transition: all .3s ease-in;
  transform: translatex(0);
}
.mode-item{
  width: 40px;
  height: 100%;
  position: relative;
  z-index: 99;
  cursor:pointer;
  color:#b0b0b0;
}
.mode-item:not(.active):hover{
  color:#5f5f5f;
}
.mode-item.active.sun{
  color:#ffbd2e;
}
.mode-item.active.moon{
  color:#264df3;
}
.mode-item.active.follow{
  color:#47b159;
}
.active-tabs-bg.moon{
  transform: translatex(40px);
}
.active-tabs-bg.follow{
  transform: translatex(80px);
}
.tip-layout{
  text-align: center;
  line-height: 1.5;
  padding:10px;
  color:#8f8f8f;
  transition: all .3s ease-in;
  opacity: 1;
}
.tip-layout-hide{
  opacity: 0;
}


/*深色模式*/

:root[mode=dark] .nav-menu-layout {
  background-color: #151212;
}

:root[mode=dark] .list-item:not(.active-item):hover {
  background-color: #242424;
}
:root[mode=dark] .list-item:not(.active-item):hover .item-name {
  color:#ccc;
}

:root[mode=dark] .item-name {
  color: #777777;
}

:root[mode=dark] .active-item .item-name {
  color: #f8f8f8;
  font-weight: normal;
}

:root[mode=dark] .active-item .item-link:before {
  background-color: #f29e26;
}
:root[mode=dark] .mode-toggle-layout{
  background-color: #242424;
  color:#383838
}
:root[mode=dark]  .active-item .item-link:before {
  background-color: #264df3;
}
:root[mode=dark] .nav-menu-layout{
  background-color: #1e1e1e;
}
</style>
