<template>
  <div class="open-layout">
    <vue-particles
        color="#ccc"
        :particleOpacity="0.7"
        :particlesNumber="60"
        shapeType="edge"
        :particleSize="4"
        linesColor="#e5e5e5"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="2"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
        class="open-bg"
    >
    </vue-particles>
    <div class="bg-box">
      <transition-group name="fade" mode="out-in">
        <img v-for="(item,index) in bgImg"
             :src="item.img"
             :key="item.id"
             v-show="index === curIndex"
             alt="背景图"
             class="bg">
      </transition-group>
    </div>
    <div class="logo">
      <img src="../assets/logo-big.png" alt="logo" class="logo-img">
      <div class="nav-group flex">
        <router-link to="/guide" class="nav-btn flex guide-btn">
          <application-one theme="filled" size="20" fill="currentColor" :strokeWidth="3"/>
          <p class="nav-title">开荒导航</p>
        </router-link>
        <router-link to="/our2021" class="nav-btn flex our-btn">
          <fire theme="filled" size="20" fill="currentColor" :strokeWidth="3"/>
          <p class="nav-title">我们2021</p>
        </router-link>
      </div>
    </div>
    <index-footer class="footer"></index-footer>
  </div>
</template>

<script>

import indexFooter from "@/components/index-footer";
export default {
  name: "open",
  components:{
    indexFooter
  },
  data() {
    return {
      bgImg: [
        {
          id:1,
          img:require("../assets/index/index-bg.jpg")
        },
        {
          id:2,
          img:require("../assets/index/index-bg2.jpg")
        },
        {
          id:3,
          img:require("../assets/index/index-bg3.jpg")
        },
        {
          id:4,
          img:require("../assets/index/index-bg4.jpg")
        }
      ],
      curIndex: 0,
    }
  },
  mounted() {
    const timer = setInterval(() => {
      if (this.curIndex === this.bgImg.length - 1) {
        this.curIndex = 0;
      } else {
        this.curIndex++;
      }
    }, 15000)

    this.$once('hook:beforeDestroy', () => {
      clearInterval(timer);
    })
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}

.open-layout {
  width: 100%;
  height: 100%;
  background: #1e1e1e;
  overflow: hidden;
}

.open-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 8;
  background: none;
}

.bg-box {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  -webkit-animation: infade 15s linear 1;
  animation: infade 15s linear 1;
}

.logo {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 90;
  height: 450px;
  width: 450px;
  margin: auto;
}

.logo-img {
  width: 200px;
  text-align: center;
  margin-bottom:15px;
}
.nav-group{
  padding-top:20px;
}
.nav-btn {
  color:black;
  background: rgba(255, 255, 255, .8);
  padding: 10px 25px;
  border-radius: 6px;
  margin: 10px 20px 0;
  cursor: pointer;
  font-size: 16px;
  animation-timing-function: ease-out;
  animation: upenter 2s linear 1;
  transition: all .4s cubic-bezier(.71,.22,.58,.84);
}
.nav-btn:hover{
  box-shadow: 0 0 16px 3px rgba(255,255,255,0.5);
  transform:translateY(-6px);
  background: rgba(255, 255, 255, .9);
}
.our-btn:hover{
  color:#da3537;
}
.guide-btn:hover{
  color:#264ef2;
}
.nav-title{
  margin-left: 5px;
  line-height: 1;
}
.footer{
  position: fixed;
  bottom:0;
  left:0;
  right:0;
  z-index: 99;
  margin: auto;
}

.fade-enter-active {
  -webkit-animation: infade 15s linear 1;
  animation: infade 15s linear 1;
}

.fade-leave-active {
  -webkit-animation: outfade 2s linear 1;
  animation: outfade 2s linear 1;
}



@keyframes upenter {
  0% {
    transform: translateY(20px);
    opacity: 0.2;
  }

  25% {
    transform: translateY(15px);
    opacity: 0.5;
  }

  50% {
    transform: translateY(10px);
    opacity: 0.8;
  }

  75% {
    transform: translateY(5px);
    opacity: 1;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes infade {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }

  25% {
    transform: scale(1.1);
    opacity: 0.75;;
  }

  50% {
    transform: scale(1.2);
    opacity: 1;
  }

  75% {
    transform: scale(1.1);
    opacity: 0.75;
  }

  100% {
    transform: scale(1);
    opacity: 0.5;
  }
}

@keyframes outfade {
  0% {
    opacity: 0.5;
  }

  25% {
    opacity: 0.4;;
  }

  50% {
    opacity: 0.3;
  }

  75% {
    opacity: 0.2;
  }

  100% {
    opacity: 0;
  }
}
</style>