<template>
  <div class="card">
    <div class="flex img-inner"  v-lazy-container="{ selector: 'img' }">
      <div class="view-img-mask flex">
         <p class="mask-txt flex">
           <zoom-in theme="outline" size="16" fill="#000000" class="zoom-in-icon"/>
           <slot>查看大图</slot>
         </p>
      </div>
      <img  :data-src="item.img"  :data-loading="loadImg"  :data-error="errorImg" alt="产品图"/>
    </div>

    <div class="prod-author flex flex-between">
      <p>{{item.name}}</p>
      <p class="flex prod-num">入选作品数：<span >{{item.num}}</span></p>
    </div>
  </div>
</template>

<script>
export default {
  name: "photo-card",
  props: ["item"],
  data(){
    return {
      loadImg:require('../assets/prod-loading.gif'),
      errorImg:require('../assets/img-error.png')
    }
  }
}
</script>

<style scoped>

.card {
  width: 285px;
  transition: all 0.1s ease;
  position: relative;
  z-index: 1;
  cursor: pointer;
  border-radius: 6px;
  overflow: hidden;

}
.card .img-inner{
  height: 310px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.card .img-inner .view-img-mask{
  position: absolute;
  background: rgba(255,255, 255, .3) !important;
  backdrop-filter: blur(4px) !important;
  opacity: 0;
  transition: opacity .2s ease-in;
  width: 100%;
  height: 100%;
  z-index: 99;
  top:0;
  left:0;
}
.view-img-mask .mask-txt{
  border-radius: 24px;
  padding:6px 12px;
  border:1px solid rgba(255,255,255,.3);
  background-color: rgba(200,200,200,.2);
}
.view-img-mask .zoom-in-icon{
  margin-right: 5px;
}
.card img {
  width: 100%;
  display: block;
  overflow: hidden;
  background: #fff;
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.card:hover .img-inner .view-img-mask{
  opacity: 1;
}
.card:hover img {
  -webkit-filter: grayscale(0.65);
  filter: grayscale(0.65);
}
.prod-author {
  background-color: #f5f5f5;
  padding: 15px;
  color: #333;
  font-size: 13px;
}

.prod-num {
  color: #999;
  font-size: 13px;
  line-height: 1.45;
}

</style>