import Vue from 'vue'
import VueRouter from "vue-router";
import App from './App.vue'
import VueParticles from "vue-particles";
import {routes} from "@/routes";
import VueAwesomeSwiper from 'vue-awesome-swiper'
import VueLazyload from "vue-lazyload";
import 'swiper/css/swiper.css'
import {
    Brain,
    PicOne,
    Material,
    DiamondThree,
    WeixinFavorites,
    Computer,
    BezierCurve,
    Platte,
    Effects,
    RobotOne,
    HamburgerButton,
    Close,
    SunOne,
    Moon,
    LaptopComputer,
    ArrowCircleUp,
    ToTop,
    Fire,
    ApplicationOne,
    CoordinateSystem,
    ContrastViewCircle,
    CarouselVideo,
    Classroom,
    ZoomIn,
    Trophy,
    DoubleDown
} from '@icon-park/vue';
import{Swiper,SwiperSlide} from "vue-awesome-swiper";
const loadimage = require('./assets/loading.gif')
const errorimage = require('./assets/img-error.png')

Vue.config.productionTip = false
Vue.use(VueParticles)
Vue.use(VueRouter)
Vue.use(VueAwesomeSwiper)
Vue.use(VueLazyload,{
    error: errorimage,
    loading: loadimage,
})

Vue.component('Brain', Brain)
Vue.component('PicOne', PicOne)
Vue.component('Material', Material)
Vue.component('DiamondThree', DiamondThree)
Vue.component('WeixinFavorites', WeixinFavorites)
Vue.component('Computer', Computer)
Vue.component('BezierCurve', BezierCurve)
Vue.component('Platte', Platte)
Vue.component('Effects', Effects)
Vue.component('RobotOne', RobotOne)
Vue.component('HamburgerButton', HamburgerButton)
Vue.component('Close', Close)
Vue.component('SunOne', SunOne)
Vue.component('Moon', Moon)
Vue.component('LaptopComputer', LaptopComputer)
Vue.component('ArrowCircleUp', ArrowCircleUp)
Vue.component('ToTop', ToTop)
Vue.component('Fire', Fire)
Vue.component('ApplicationOne', ApplicationOne)
Vue.component('CoordinateSystem', CoordinateSystem)
Vue.component('ContrastViewCircle', ContrastViewCircle)
Vue.component('CarouselVideo', CarouselVideo)
Vue.component('Classroom', Classroom)
Vue.component('ZoomIn', ZoomIn)
Vue.component('Trophy', Trophy)
Vue.component('DoubleDown', DoubleDown)
Vue.component( 'Swiper', Swiper)
Vue.component( 'SwiperSlide', SwiperSlide)


const router = new VueRouter({
    routes
})

new Vue({
    router,
    render: h => h(App),
}).$mount('#app')
