<template>
  <div class="mobile-header-layout flex flex-between">
    <div class="logo-wrap">
      <img :src="setLogoImg" alt="logo" class="mobile-logo">
    </div>
    <div>
      <transition name="rotate" mode="out-in">
        <component
            :is=" isClose ? 'Close':'HamburgerButton'"
            theme="outline"
            size="28"
            :strokeWidth="3"
            :fill="getMode==='dark'?'#f5f5f5':'#000'"
            @click="toggleNav"
            class="nav-icon"
        >
        </component>
      </transition>
    </div>
  </div>
</template>

<script>
import {store} from "@/store";

export default {
  name: "mobile-header",
  data() {
    return {
      isClose: this.close,
      scrollHeight: ''
    }
  },
  props: {
    close: {
      type: Boolean
    }
  },

  watch: {
    close(newValue) {
      this.isClose = newValue
    }
  },
  computed:{
    setLogoImg(){
      return store.state.mode==='dark' ? require('../assets/mobile-logo-dark.png') : require('../assets/mobile-logo.png')
    },
    getMode(){
      return store.state.mode
    }
  },
  methods: {
    toggleNav() {
      this.isClose = !this.isClose
      if (this.isClose) {
        this.scrollHeight = window.scrollY
        if(!this.timer){
          this.timer = setInterval(()=>{
            document.querySelector('html').classList.add('noscroll')
            clearInterval(this.timer);
            this.timer=null;
          },300)
        }

      } else {
        document.querySelector('html').classList.remove('noscroll')
        document.documentElement.scrollTop = document.body.scrollTop = this.scrollHeight
      }
      this.$emit('toggle-nav', this.isClose)
    }
  }
}
</script>

<style scoped>
.mobile-header-layout {
  background: #fff;
  padding: 15px 25px;
  border-bottom: 1px solid #f3f3f3;
}

.logo-wrap {
  height: 56px;
}

.mobile-logo {
  height: 100%;
}

.nav-icon {
  padding: 8px;
}

.rotate-enter-active {
  transition: all .3s cubic-bezier(0.5, 0.7, 0.8, 1.0);
}

.rotate-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.3, 1.0);

}
.rotate-enter-to{
  transform: rotate(180deg);
}
.rotate-enter, .rotate-leave-to {
  transform: rotate(90deg);
  opacity: 0;
}
:root[mode=dark] .mobile-header-layout{
  background-color: #1e1e1e;
  box-shadow: 0 2px 4px rgba(0,0,0,.1);
  border-bottom: none;
}


</style>
