import Guide from "@/pages/guide";
import Pioneer from "@/pages/pioneer";
import Our2021 from "@/pages/our2021/our2021";
import weekExercise from "@/pages/our2021/week-exercise";
import week3d from "@/pages/our2021/week-3d";
import plan from "@/pages/our2021/week-plan";
import animation from "@/pages/our2021/week-animation"
import threeBody from "@/pages/three-body";
import commune from "@/pages/commune";
import m from "@/pages/m";

export const routes = [
    {
        path: '',
        component: Pioneer
    },
    {
        path: '/guide',
        component: Guide
    },
    {
        path: '/our2021',
        component: Our2021
    },
    {
        path: '/our2021/weekExercise',
        name:'weekExercise',
        component: weekExercise,
        redirect: '/our2021/weekExercise/3d',
        children: [
            {
                path: '3d',
                name:'week3d',
                component: week3d
            },
            {
                path:'plan',
                name:'week-plan',
                component: plan
            },
            {
                path:'animation',
                name:'week-animation',
                component: animation
            },
            {
                path: '*',
                redirect: '/our2021/weekExercise/3d',
            }
        ]
    },
    {
        path: '/our2021/threeBody',
        component: threeBody
    },
    {
        path: '/our2021/commune',
        component: commune
    },
    {
        path: '/our2021/m',
        component: m
    }
]