<template>
  <div class="nav-layout">
    <MobileHeader class="mobile-header" v-on:toggle-nav="showNavHandle" :close="isHide"></MobileHeader>
    <NavMenu class="nav-menu" v-if="notMobile"></NavMenu>
    <transition name="slide-fade">
      <NavMobileMenu class="nav-mobile-menu" v-show="showMobileNav" v-on:toggle-nav="showNavHandle"></NavMobileMenu>
    </transition>
    <NavContent class="nav-content"></NavContent>
    <NavFooter class="footer-content"></NavFooter>
    <div v-if="!notMobile" v-show="showArrow" class="go-top-wrap flex" @click="goTop">
      <to-top theme="filled" size="24" fill="currentColor" :strokeWidth="3" />
    </div>
  </div>
</template>

<script>
import NavMenu from "@/components/nav-menu"
import NavContent from "@/components/nav-content"
import NavFooter from "@/components/nav-footer"
import MobileHeader from "@/components/mobile-header";
import NavMobileMenu from "@/components/nav-mobile-menu";

import {action} from '@/store'

export default {
  name: "layout",
  data() {
    return {
      notMobile: false,
      showMobileNav: false,
      isHide: false,
      showArrow:false
    }
  },
  components: {
    NavMenu,
    NavContent,
    NavFooter,
    MobileHeader,
    NavMobileMenu
  },
  created() {
    // localStorage.removeItem('mode');
    // localStorage.removeItem('follow');
    const screenWidth = screen.width || window.innerWidth || document.documentElement.clientWidth
    if (screenWidth > 500) {
      this.notMobile = true
    }
    const systemMode = window.matchMedia('(prefers-color-scheme:dark)')
    systemMode.onchange = ()=>{
      action.initMode()
    }
   action.initMode("init")
  },
  mounted(){
    document.addEventListener('scroll', this.showUpArrow,true)
  },
  methods: {
   showUpArrow(e){
      let scrollTop = e.target.scrollTop || document.documentElement.scrollTop || document.body.scrollTop
      let screenHeight=window.screen.height
      this.showArrow = scrollTop > screenHeight;
    },
    goTop(){
      document.querySelector('body').scrollIntoView({
        behavior: 'smooth'
      })
    },
    showNavHandle(isClose) {
      this.showMobileNav = isClose
      this.isHide = isClose
    }
  }
}
</script>

<style scoped>
.nav-layout {
  width: 100%;
  min-height: 100%;
  align-items: stretch;
  position: relative;
  overflow: hidden;
}

.nav-menu {
  width: 240px;
  height: 100%;
  max-width: 400px;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  transform: translateZ(0);
  box-sizing: border-box;
}

.nav-content,
.footer-content {
  width: 100%;
  box-sizing: border-box;
  padding-left: 240px;
}

.mobile-header {
  position: fixed;
  top: -90px;
  left: 0;
  right: 0;
  transform: translateZ(0);
  z-index: 998;
}

.nav-mobile-menu {
  position: fixed;
  top: 86px;
  left: 0;
  right: 0;
  bottom: 0;
  transform: translateZ(0);
  z-index: 888;
}
.go-top-wrap{
  position: fixed;
  bottom: 40px;
  right: 20px;
  color:#5d5d5d;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color:rgba(160,160,160,.5);
}
:root[mode=dark] .go-top-wrap{
  color: #b9b9b9;
  background-color:rgba(100,100,100,.5);
}

.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(0.4, 0, 0, 0.2);
  top: 86px;
}

.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0.5;
  top: 150px;
}


@media screen and (max-width: 840px) {
  .nav-content,
  .footer-content {
    padding-left: 170px;
  }

  .nav-menu {
    width: 170px;
  }
}

@media screen and (min-width: 501px) {
  .nav-mobile-menu {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .nav-menu {
    transform: translate(-180px, 0);
    transition: transform 0.5s ease-in;
  }

  .nav-content,
  .footer-content {
    padding-left: 15px;
    padding-right: 15px;
  }

  .mobile-header {
    transform: translate(0, 90px);
    transition: transform 0.5s ease-in;
  }
}


</style>
